html, body {
    height: 100%;
}

html {
    display: table;
    margin: auto;
}

body {
    margin-top: 10%;
    background-color: #FDFFF6;
}

.heading {
    font-family: Breathing;
    font-size: 400%;
    display: table;
    margin: auto;
    margin-top: 10%;
}

.markdownContainer {
  border-radius: 10px;
  border: 2px solid #a6a6a6;
  background-color: #F7F6FF;
  width: 600px;
  display: table;
  margin: auto;
  margin-top: 10%;
}

.markdownBody {
  padding: 10%;
}

@font-face {
  font-family: Breathing;
  src: url(fonts/Breathing.ttf);
}
